<template>
    <div class='hot-article'>
        <template v-if='result.length'>
            <div class='hot-title'><span><img :src='hotImage'/></span><span>热门文章</span></div>
            <div class='hot-content' v-for='(item, index) in result' :key='index' @click='getDetail(item.id)'>
                <div class='first'><span><img :src=getSrc(index) alt=''/></span><span
                    :title='item.title'>{{item.title}}</span></div>
                <div class='second'>
                    <span><a href='javascript:void(0)' target="_blank" @click='getIntroduction(item.author)'>{{item.author}}</a></span>
                    <span>{{' | '}}</span>
                    <span>{{getUpdateTime(index)}}</span>
                </div>
            </div>
        </template>
        <template v-else>
            <div class='hot-image'>
                <img src="../../assets/empty.png"/>
            </div>
        </template>
    </div>
</template>

<script>
    import DateUtil from '../../utils/DateUtil';
    import EmptyView from '@/components/util/EmptyView';

    export default {
        name: 'HotArticle',
        components: {EmptyView},
        props: {
            result: {
                type: Array,
                default: () => []
            }
        },
        data () {
            return {
                hotImage: require('../../assets/fire/hot.svg')
            };
        },
        mounted () {
            if (document.getElementsByClassName('above-info')[0].style.marginTop !== '0rem') {
                document.getElementsByClassName('hot-article')[0].style.top = 9 - document.body.clientHeight / 200 + 'rem';
            } else {
                document.getElementsByClassName('hot-article')[0].style.top = '9rem';
            }
        },
        methods: {
            getUpdateTime (index) {
                let scope = this;
                return DateUtil.formatDate(new Date(scope.result[index].updateTime));
            },
            getIntroduction (author) {
                let scope = this;
                let routerData = scope.$router.resolve({
                    path: '/introduction',
                    query: {
                        author: author
                    }
                });
                window.open(routerData.href, '_blank');
            },
            getSrc (index) {
                return require('../../assets/fire/fire' + index + '.svg');
            },
            getDetail (id) {
                let scope = this;
                let routerData = scope.$router.resolve({
                    path: '/detail',
                    query: {
                        id: id
                    }
                });
                window.open(routerData.href, '_blank');
            }
        }
    }
    ;
</script>

<style lang='scss'>
    .hot-article {
        width: 18%;
        height: 50%;
        position: fixed;
        right: 10rem;
        /*top: 9rem;*/
        background: white;
        transition: all .4s ease-in-out;

        .hot-title {
            width: 100%;
            height: 10%;
            display: flex;
            justify-content: center;
            align-items: center;
            font-weight: bold;

            span:nth-child(1) {
                padding-top: .4rem;
                padding-right: .5rem;

                img {
                    transform: scale(1.2);
                }
            }
        }

        .hot-content {
            width: 100%;
            height: 18%;
            border-top: 1px solid rgba(180, 180, 180, 0.36);

            :hover {
                cursor: pointer;
            }

            .first {
                height: 60%;
                width: 100%;
                display: flex;
                justify-content: flex-start;
                align-items: center;

                span:nth-child(1) {
                    width: 10%;
                }

                span:nth-child(2) {
                    width: 90%;
                    text-align: left;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;

                    &:hover {
                        text-decoration: underline;
                        color: #409eff;
                    }
                }
            }

            .second {
                height: 40%;
                width: 100%;
                font-size: .7rem;
                color: #d2d2d2;
                display: flex;
                justify-content: flex-start;

                span:nth-child(1) {
                    padding-left: 2rem;
                    a {
                        color: unset;
                        text-decoration: unset;
                    }

                    :hover {
                        color: #409eff;
                    }
                }

                span:nth-child(2) {
                    padding: 0 .2rem;
                }
            }
        }

        .hot-image {
            width: 100%;
            height: 100%;

            img {
                width: 95%;
                height: 50%;
                position: relative;
                top: 6rem;
            }
        }
    }

</style>



